<template>
  <section>
    <el-tabs v-model="activeTabName" @tab-click="handleTabClick" style="float:left;width:100%;">
      <el-tab-pane label="" name="BundleManage"></el-tab-pane>
      <el-tab-pane label="" name="AddBundle"></el-tab-pane>
    </el-tabs>

    <div style="float: left;width: 100%">
      <el-select size="small" v-model="filters.deviceModel" placeholder="" style="float: left;margin-left: 5px;width:180px;">
        <el-option v-for="item in deviceModelOptions" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <el-select size="small" v-model="filters.sourceType" placeholder="" style="float: left;margin-left: 10px;width:180px;">
        <el-option v-for="item in sourceTypeOptions" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <el-select size="small" v-model="filters.userId" filterable placeholder="" style="float: left;margin-left:10px;width:200px;">
        <el-option v-for="item in users" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>
      <el-input size="small" v-model="filters.keyword" style="float: left;margin-left: 15px;width:200px;" placeholder=""></el-input>
      <el-button size="small" @click="getResources(1)" style="float: left;margin-left: 10px;"></el-button>

      <el-dropdown style="float: left;margin-left: 20px;">
        <el-button type="primary" size="small">
          LDAP<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-on:click.native="showHandleSyncToLdapDialog()">LDAP</el-dropdown-item>
          <el-dropdown-item v-on:click.native="handleSyncFromLdap()">LDAP</el-dropdown-item>
          <el-dropdown-item v-on:click.native="handleCleanUpLdap()">LDAP</el-dropdown-item>

        </el-dropdown-menu>
      </el-dropdown>

      <el-upload class="upload-demo" :action=uploadUrl name="filePoster" :headers="myHeaders" :show-file-list="false" :on-success="uploadSuccess" :on-error="uploadError" :on-progress="onprogress" style="float: left; margin-left: 10px;">
        <el-button type="primary" size="small"></el-button>
      </el-upload>

      <el-button type="primary" size="small" @click="exportBundle" style="float: left; margin-left: 10px;">
      </el-button>
      <el-button type="primary" size="small" @click="showMultiSetAccessLayerDialog" style="float: left; margin-left: 10px;">
      </el-button>
      <el-button type="danger" size="small" @click="handleMultiDelete" style="float: left; margin-left: 10px;">
        
      </el-button>
      <el-button type="danger" size="small" @click="syncUser" style="float: left; margin-left: 10px;">
        
      </el-button>

      <!--
            <el-button type="primary" size="small" v-on:click="handleCleanUpLdap()" style="float: right;margin-right: 10px;">LDAP</el-button>
            <el-button type="primary" size="small" v-on:click="showHandleSyncToLdapDialog()" style="float: right;margin-right: 10px;">LDAP</el-button>
            <el-button type="primary" size="small" v-on:click="handleSyncFromLdap()" style="float: right;margin-right: 10px;">LDAP</el-button>
            -->
    </div>

    <!---->
    <el-table :data="resources" v-loading="resourceTableLoading" @selection-change="handleSelectionChange" style="float: left;width: 100%;margin-top: 20px;">
      <el-table-column width="50" type="selection"></el-table-column>
      <el-table-column prop="bundleName" label="" width="200" sortable>
      </el-table-column>
      <el-table-column prop="deviceModel" label="" width="120" sortable>
      </el-table-column>
      <el-table-column prop="bundleAlias" label="" width="130" sortable>
      </el-table-column>
      <el-table-column prop="username" label="" width="200">
      </el-table-column>
      <el-table-column prop="accessNodeUid" label="" width="220" sortable>
      </el-table-column>
      <el-table-column width="100" label="">
        <template slot-scope="scope">
          <div v-if="scope.row.onlineStatus=='ONLINE'" style="height:20px;width:20px;border-radius:50%;background-color:#0f0"></div>
          <div v-else style="height:20px;width:20px;border-radius:50%;background-color:#f00"></div>
        </template>
      </el-table-column>

      <el-table-column width="100" label="">
        <template slot-scope="scope">
          <div v-if="scope.row.lockStatus=='IDLE'"></div>
          <div v-else-if="scope.row.lockStatus=='BUSY'"></div>
        </template>
      </el-table-column>

      <el-table-column width="100" label="">
        <template slot-scope="scope">
          <div v-if="scope.row.sourceType=='EXTERNAL'">LDAP</div>
          <div v-else>BVC</div>
        </template>
      </el-table-column>

      <el-table-column label="" width="250">
        <template slot-scope="scope">
          <el-button type="text" @click="handleDetail(scope.row)" size="small"></el-button>
          <el-button type="text" v-if="scope.row.sourceType!='EXTERNAL'" @click="handleModify(scope.row)" size="small"></el-button>
          <el-button type="text" v-if="scope.row.sourceType!='EXTERNAL'" @click="handleSetLayerId(scope.row)" size="small"></el-button>
          <!--<el-button type="text" v-if="scope.row.onlineStatus=='ONLINE'" @click="handleLogout(scope.row)" size="small"></el-button>-->
          <el-button type="text" v-if="scope.row.sourceType!='EXTERNAL'" @click="handleClear(scope.row)" size="small"></el-button>
          <el-button type="text" v-if="scope.row.sourceType!='EXTERNAL'" @click="handleDelete(scope.row)" size="small"></el-button>
        </template>
      </el-table-column>

      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <!--<el-button type="text" v-if="scope.row.bundleType!='VenusTerminal'" @click="abilityConfig(scope.row)" size="small"></el-button>-->
          <el-button type="text" v-if="scope.row.sourceType!='EXTERNAL'" @click="abilityConfig(scope.row)" size="small"></el-button>
          <el-button type="text" v-if="scope.row.sourceType!='EXTERNAL'" @click="abilityDetail(scope.row)" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-input size="small" v-model="filters.countPerPage" style="float: right;margin-right: 30px;width:200px;" placeholder=",20" @change="pageChange"></el-input>
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentPageChange" :current-page="pageNum" :page-size="countPerPage" :total="total" style="float:right;">
      </el-pagination>
    </el-col>

    <el-dialog title="" :visible.sync="detailDialogVisible" width="30%">
      <ul>
        <li v-for="detailInfo in detailInfos">
          {{ detailInfo.name }} : {{ detailInfo.value }}
        </li>
      </ul>
    </el-dialog>

    <el-dialog title="" :visible.sync="abilityDetailDialogVisible" width="40%">
      <el-table :data="channelSchemes">
        <el-table-column property="channelId" label="ID" width="250"></el-table-column>
        <el-table-column property="channelName" label="" width="250"></el-table-column>
        <el-table-column width="150" label="">
          <template slot-scope="scope">
            <div v-if="scope.row.channelStatus=='IDLE'"></div>
            <div v-else-if="scope.row.channelStatus=='BUSY'"></div>
            <div v-else-if="scope.row.channelStatus=='OFFLINE'"></div>
            <div v-else></div>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <el-dialog title="" :visible.sync="setAccessLayerVisible" width="30%">
      <div style="margin-top:10px;">
        <el-input v-model="newAccessNodeUid" placeholder="ID" style="width: 400px;" readOnly @click.native="handleSelectLayerNode">
          <template slot="prepend">ID</template>
        </el-input>
      </div>
      <div style="margin-top:30px;">
        <el-button type="primary" style="margin-left:150px;" @click="submitSetAccessLayer()"></el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="confirmSyncToLdapVisible" width="500px" style="margin-top:260px;">
      <div>
        LDAP
      </div>
      <div style="margin-top:40px;">
        <el-button size="small" style="margin-left:130px;" @click="confirmSyncToLdapVisible = false"> 
        </el-button>
        <el-button type="primary" size="small" style="margin-left:10px;" @click="handleSyncToLdap('false')">
          
        </el-button>
        <el-button type="primary" size="small" style="margin-left:10px;" @click="handleSyncToLdap('true')">
          
        </el-button>
      </div>
    </el-dialog>

    <template>

      <selectLayerNode ref="selectLayerNode" @layer-node-selected="layerNodeSelected"></selectLayerNode>

    </template>

  </section>

</template>

<script type="text/ecmascript-6">
import {  getAllUsers, getDeviceModels, getBundles, getBundleDetailInfo, deleteBundle, getBundleChannels, logoutBundle, clearBundle, setAccessLayer, syncLdap, syncEquipInfoFromLdap,
  syncEquipInfToLdap, cleanUpEquipInfo, exportBundle, syncUser} from '../../api/api';
// let requestIP = document.location.host.split(":")[0];

import selectLayerNode from '../layernode/SelectLayerNode';

let basePath = document.location.origin;

export default {
  components: { selectLayerNode },
  data () {
    return {
      activeTabName: "BundleManage",
      resources: [],
      deviceModelOptions: [],
      filters: {
        deviceModel: '',
        keyword: '',
        userId: '',
        sourceType: '',
        countPerPage: ''
      },
      users: [],
      resourceTableLoading: false,
      detailDialogVisible: false,
      abilityDetailDialogVisible: false,
      setAccessLayerVisible: false,
      confirmSyncToLdapVisible: false,
      detailInfos: [],
      channelSchemes: [],
      total: 0,
      pageNum: 1,
      countPerPage: 20,
      currentRow: {},
      bundleId: "",
      newAccessNodeUid: "",
      // uploadUrl : "http://" + requestIP + ":8887/suma-venus-resource/bundle/import",
      uploadUrl: basePath + "/bundle/import",
      myHeaders: {
        'tetris-001': sessionStorage.getItem('token')
      },

      sourceTypeOptions: [
        {
          value: "",
          label: ""
        },
        {
          value: "SYSTEM",
          label: "BVC"
        },
        {
          value: "EXTERNAL",
          label: "LDAP"
        }
      ],
      multipleSelection: []
    }
  },
  methods: {
    pageChange: function () {
      this.getResources(this.pageNum);
    },
    handleSelectLayerNode: function () {
      var self = this;
      self.$refs.selectLayerNode.show();
    },
    layerNodeSelected: function (row, done) {
      var self = this;
      self.newAccessNodeUid = row.nodeUid;
      done();
    },
    handleTabClick (tab, event) {
      if ("BundleManage" !== tab.name) {
        this.$router.push('/' + tab.name);
      }
    }
    ,
    //
    getAllUsers: function () {
      getAllUsers().then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          this.users = [];
          this.users.push({
            id: -1,
            name: ""
          });
          this.users = this.users.concat(res.users);
        }
      });
    }
    ,
    //
    getDeviceModels: function () {
      getDeviceModels().then(res => {
        if (!res.errMsg && res.deviceModels) {
          this.deviceModelOptions.push({
            value: "",
            label: ""
          });
          for (let deviceModel of res.deviceModels) {
            let deviceModelOption = {
              value: deviceModel,
              label: deviceModel
            };
            this.deviceModelOptions.push(deviceModelOption);
          }
        }
      });
    }
    ,
    //
    getResources: function (pageNum) {

      this.countPerPage = 20
      if (/^[1-9]+[0-9]*]*$/.test(this.filters.countPerPage)) {
        this.countPerPage = parseInt(this.filters.countPerPage)
      }

      if (this.countPerPage * (pageNum - 1) > this.total) {
        pageNum = Math.ceil(this.total / this.countPerPage);
      }

      let param = {
        deviceModel: this.filters.deviceModel,
        keyword: this.filters.keyword,
        sourceType: this.filters.sourceType,
        userId: this.filters.userId,
        pageNum: pageNum,
        countPerPage: this.countPerPage
      };
      this.resourceTableLoading = true;
      getBundles(param).then((res) => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          this.pageNum = pageNum;
          this.total = res.total;
          this.resources = res.resources;
        }

        this.resourceTableLoading = false;
      });
    }
    ,
    //
    handleCurrentPageChange (val) {
      this.pageNum = val;
      this.getResources(this.pageNum);
    }
    ,
    //
    handleDetail: function (row) {
      this.resourceTableLoading = true;
      let param = {
        bundleId: row.bundleId
      };
      getBundleDetailInfo(param).then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          this.detailDialogVisible = true;
          this.detailInfos = res.extraInfos;
          this.detailInfos.push({
            name: "bundle_id",
            value: row.bundleId
          });
        }
        this.resourceTableLoading = false;
      });
    }
    ,
    handleModify: function (row) {
      console.log(JSON.stringify(row));

      this.$router.push({
        path: '/ModifyBundle',
        query: {
          bundleId: row.bundleId,
          bundleName: row.bundleName,
          deviceIp: row.deviceAddr.deviceIp,
          devicePort: row.deviceAddr.devicePort,
          multicastEncode: row.multicastEncode,
          multicastEncodeAddr: row.multicastEncodeAddr,
          multicastDecode: row.multicastDecode
        }
      });
    }
    ,
    handleLogout: function (row) {
      this.resourceTableLoading = true;
      let param = {
        bundleId: row.bundleId
      };

      logoutBundle(param).then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          row.onlineStatus = "OFFLINE";
          this.$message({
            message: "",
            type: 'success'
          });
        }
        this.resourceTableLoading = false;
      });
    }
    ,
    handleClear: function (row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        this.resourceTableLoading = true;
        let param = {
          bundleId: row.bundleId
        };
        clearBundle(param).then(res => {

          if (res.errMsg) {
            this.$message({
              message: res.errMsg,
              type: 'error'
            });
          } else {
            this.$message({
              message: "",
              type: 'success'
            });
          }
          this.resourceTableLoading = false;
        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
    ,
    handleDelete: function (row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.resourceTableLoading = true;
        let param = {
          bundleIds: row.bundleId
        };

        deleteBundle(param).then(res => {
          this.resourceTableLoading = false;
          if (res.errMsg) {
            this.$message({
              message: res.errMsg,
              type: 'error'
            });
          } else {
            this.$message({
              message: "",
              type: 'success'
            });
            this.getResources(1);
            // window.location.reload();
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
    ,
    abilityConfig: function (row) {
      this.$router.push({
        path: '/ConfigBundle',
        query: {
          bundleId: row.bundleId
        }
      });
    }
    ,
    abilityDetail: function (row) {
      let param = {
        bundleId: row.bundleId
      };

      this.resourceTableLoading = true;
      getBundleChannels(param).then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          this.abilityDetailDialogVisible = true;
          this.channelSchemes = res.channelSchemes;
        }

        this.resourceTableLoading = false;
      });
    }
    ,
    handleSetLayerId: function (row) {
      this.currentRow = row;
      this.bundleId = row.bundleId;
      this.newAccessNodeUid = row.accessNodeUid;
      this.setAccessLayerVisible = true;
    }
    ,

    submitSetAccessLayer: function () {
      let param = {
        bundleId: this.bundleId,
        accessLayerId: this.newAccessNodeUid
      };

      setAccessLayer(param).then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          this.currentRow.accessNodeUid = this.newAccessNodeUid;
          this.$message({
            message: "",
            type: 'success'
          });
        }
        this.setAccessLayerVisible = false;
      });
    }
    ,

    showMultiSetAccessLayerDialog: function () {
      if (!this.multipleSelection.length) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }

      let bundleIdArr = [];

      for (let selection of this.multipleSelection) {
        bundleIdArr.push(selection.bundleId);
      }

      this.bundleId = bundleIdArr.join(",");

      console.log("bundleIds==" + this.bundleId);

      this.newAccessNodeUid = "";
      this.setAccessLayerVisible = true;
    }
    ,

    exportBundle: function () {

      exportBundle(null).then((res) => {
        const blob = new Blob([res.data], { type: 'application/octet-stream;charset=utf-8' });

        const fileName = 'folder.csv';

        const elink = document.createElement('a');

        elink.download = fileName;

        elink.style.display = 'none';

        elink.href = URL.createObjectURL(blob);

        document.body.appendChild(elink);

        elink.click();

        URL.revokeObjectURL(elink.href);

        document.body.removeChild(elink);

      }).catch((error) => {

        this.$message({

          message: error,

          type: 'error'

        });

      })

    }
    ,

    uploadSuccess: function (res) {
      if (res.errMsg) {
        this.$message({
          message: res.errMsg,
          type: 'error'
        });
      } else {
        if (res.successCnt) {
          this.$message({
            message: "" + res.successCnt,
            type: 'success'
          });
        } else {
          this.$message({
            message: "",
            type: 'error'
          });
        }

        this.getResources(1);
      }
    }
    ,
    uploadError: function (err) {
      this.$message({
        message: ": " + JSON.stringify(err),
        type: 'error'
      });
    }
    ,
    onprogress: function () {
      this.resourceTableLoading = true;
    }
    ,

    handleMultiDelete: function () {
      if (!this.multipleSelection.length) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }

      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.resourceTableLoading = true;
        let bundleIdArr = [];
        for (let selection of this.multipleSelection) {
          bundleIdArr.push(selection.bundleId);
        }
        let param = {
          bundleIds: bundleIdArr.join(",")
        };

        deleteBundle(param).then(res => {
          this.resourceTableLoading = false;
          if (res.errMsg) {
            this.$message({
              message: res.errMsg,
              type: 'error'
            });
          } else {
            this.$message({
              message: "",
              type: 'success'
            });
            this.getResources(1);
            // window.location.reload();
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    }
    ,

    syncUser: function () {

      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.resourceTableLoading = true

        syncUser(null).then(res => {
          this.resourceTableLoading = false;
          if (res.errMsg) {
            this.$message({
              message: res.errMsg,
              type: 'error'
            });
          } else {
            this.$message({
              message: "",
              type: 'success'
            });
            this.getResources(1);
            // window.location.reload();
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    }
    ,

    handleSelectionChange: function (val) {
      this.multipleSelection = val;
    }
    ,

    handleSyncFromLdap: function () {
      this.$confirm("LDAP", "", {}).then(() => {
        this.resourceTableLoading = true;
        //NProgress.start();

        let para = {};
        syncEquipInfoFromLdap(para).then(res => {
          if (null !== res.errMsg && res.errMsg !== "") {
            this.$message({
              message: res.errMsg,
              type: "error",
              duration: 3000
            });
          } else {
            this.$message({
              message: ", " + res.successCnt + "",
              type: "success"
            });

            this.getResources(1);
          }
        });

        this.resourceTableLoading = false;
      });


    }
    ,

    /*
     handleSyncToLdap: function(){
     this.$confirm("LDAP", "", {}).then(() => {
     this.resourceTableLoading = true;
     //NProgress.start();

     let para = {};

     syncEquipInfToLdap(para).then(res => {
     if (null !== res.errMsg && res.errMsg !== "") {
     this.$message({
     message: res.errMsg,
     type: "error",
     duration: 3000
     });
     } else {
     this.$message({
     message: ", " + res.successCnt + "",
     type: "success"
     });

     this.getResources(1);
     }
     });

     this.resourceTableLoading = false;
     });

     },
     */

    showHandleSyncToLdapDialog: function () {
      this.confirmSyncToLdapVisible = true;
    }
    ,

    handleSyncToLdap: function (value) {
      // console.log("handleSyncToLdap value=" + value);
      this.resourceTableLoading = true;
      //NProgress.start();

      let para = {
        syncAll: value
      };

      syncEquipInfToLdap(para).then(res => {
        if (null !== res.errMsg && res.errMsg !== "") {
          this.$message({
            message: res.errMsg,
            type: "error",
            duration: 3000
          });
        } else {
          this.$message({
            message: ", " + res.successCnt + "",
            type: "success"
          });

          this.getResources(1);
        }
        this.confirmSyncToLdapVisible = false;
      });

      this.resourceTableLoading = false;

    }
    ,

    handleCleanUpLdap: function () {
      this.$confirm("LDAPLDAP", "", {}).then(() => {
        this.resourceTableLoading = true;
        //NProgress.start();

        let para = {};

        cleanUpEquipInfo(para).then(res => {
          if (null !== res.errMsg && res.errMsg !== "") {
            this.$message({
              message: res.errMsg,
              type: "error",
              duration: 3000
            });
          } else {
            this.$message({
              message: "",
              type: "success"
            });

            this.getResources(1);
          }
        });

        this.resourceTableLoading = false;
      });

    }
    ,


  },
  mounted () {
    var self = this;
    this.$nextTick(function () {
      self.$parent.$parent.$parent.$parent.$parent.setActive('/BundleManage');
    });
    this.getDeviceModels();
    this.getAllUsers();
    this.getResources(1);

    if (this.uploadUrl.indexOf('__requestIP__') !== -1) {
      var requestIP = document.location.host.split(':')[0]
      this.uploadUrl = this.uploadUrl.replace('__requestIP__', requestIP)
    }
  }
}

</script>

