<template>
  <div class="record">
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="0">
        <!---->
        <div class="search">
          <el-input placeholder="" v-model="filterText" style="width:300px; border:0;"></el-input>
          <el-select v-model="recordMode" placeholder="">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
          <el-date-picker v-model="date" type="datetimerange" range-separator="" start-placeholder="" end-placeholder="" v-if="recordMode!=''">
          </el-date-picker>
          <el-button type="default" @click="search"></el-button>
        </div>
        <!---->
        <template>
          <el-table :data="pageData">
            <el-table-column prop="fileName" label=""></el-table-column>
            <el-table-column prop="type" label="">
              <template></template>
            </el-table-column>
            <el-table-column prop="videoSource" label=""></el-table-column>
            <!-- <el-table-column prop="audioSource" label=""></el-table-column> -->
            <el-table-column prop="startTime" label="">
            </el-table-column>
            <el-table-column prop="endTime" label=""></el-table-column>
            <el-table-column prop="status" label=""></el-table-column>
            <el-table-column label="" width="250">
              <template slot-scope="scope">
                <el-button type="text" style="padding:0; font-size:20px;" title="">
                  <span class="feather-icon-log-out" @click="downLoadTask(scope.row)"></span>
                </el-button>
                <el-button type="text" style="padding:0; font-size:20px;" v-if="scope.row.status==''" title="">
                  <span class="feather-icon-pause-circle" @click="stopTask(scope.row)"></span>
                </el-button>
                <template v-else>
                  <el-button type="text" style="padding:0; font-size:20px;" title="">
                    <span class="feather-icon-youtube" @click="startTask(scope.row)"></span>
                  </el-button>
                  <el-button type="text" title="" style="padding:0; font-size:20px;">
                    <span class="icon-trash" @click="delTask(scope.row)"></span>
                  </el-button>
                </template>
                <!-- <el-tag type="primary" v-else></el-tag> -->
              </template>
            </el-table-column>
          </el-table>
        </template>
        <div style="text-align: right;background-color: #f2f2f2;">
          <el-pagination background @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize" :pager-count="5" layout="total, prev, pager, next" :total="filterDatas.length">
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="1">
        <div style="height:50px">
          <el-select v-model="Mode" placeholder="">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
        </div>

        <el-input v-model="inputDevice" placeholder="" style="margin:10px 0" @click.native="handleDeviceDia"></el-input>
        <el-input v-model="inputName" placeholder="" style="margin:10px 0"></el-input>
        <el-input v-model="inputBit" placeholder="" style="margin:10px 0" v-if="Mode===''"></el-input>

        <el-date-picker v-if="Mode===''" v-model="date" type="datetimerange" range-separator="" start-placeholder="" end-placeholder="">
        </el-date-picker>

        <div v-if="Mode=='' || Mode===''">
          <el-date-picker v-model="startMonth" type="datetime" placeholder="" default-time="12:00:00">
          </el-date-picker>
          <span></span>
          <el-date-picker v-model="endMonth" type="datetime" placeholder="" default-time="12:00:00">
          </el-date-picker>
        </div>

        <!---->
        <div style="margin:10px 0;height:50px; width:95%; box-sizing:border-box;">
          <el-button size="small" @click="submit" type="primary"></el-button>
        </div>
      </el-tab-pane>
    </el-tabs>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <div class="custom-tree-container" v-loading="treeLoading">
        <div>
          <el-tree ref="tree" :data="tree" default-expand-all node-key="id" highlight-current :expand-on-click-node="false" :props="defaultProps" @node-click="handleNodeClick">
          </el-tree>
          <!-- <el-tree :data="tree" :props="defaultProps" @node-click="handleNodeClick"></el-tree> -->
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <!-- <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button> -->
      </div>
    </el-dialog>
  </div>
</template>

<style scoped>
.search {
  height: 45px;
  line-height: 45px;
}
</style>

<script>
import { getRecordDatas, initFolderTree } from '../../api/api'
export default {
  data: function () {
    return {
      filterText: '',
      recordMode: '', //  MANUAL()|SCHEDULING()
      date: '', //
      currentPage: 1,
      tasks: [],
      filterDatas: [],
      pageSize: 5,
      total: 0,
      stopId: "",
      Mode: '', //
      activeName: '0',
      inputName: '',
      inputDevice: '',
      date: '', //
      startMonth: '',
      endMonth: '',
      inputBit: '',
      dialogFormVisible: false,
      treeLoading: false,
      tree: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      defaultExpandKeys: [],
      currentSelectNode: null,
      expandNodeKeysArr: [],
    }
  },
  computed: {
    pageData: function () {
      return this.filterDatas.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
    }
  },
  methods: {
    //
    handleCurrentChange: function (val) {
      this.currentPage = val;
    },
    handleDeviceDia: function () {
      this.initTree();
      this.dialogFormVisible = true;

    },
    //  data: type:
    search: function () {
      var self = this;
      var mode = this.recordMode;
      var name = this.filterText;
      var date = this.date;
      var page = this.currentPage;
      self.filterData(self.tasks, mode, name, date);
    },
    // 
    filterData: function (data, type, name, date) {
      var self = this;
      var startDate = this.format(date[0]);
      var endDate = this.format(date[1]);
      var temArr;
      if (!type && !name && !date) {
        return;
      }
      if (type) {
        temArr = data.filter(function (item) {
          return item.type.indexOf(type) > -1;
        })
      }
      if (name) {
        temArr = data.filter(function (item) {
          return item.fileName.indexOf(name) > -1;
        })
      }
      if (date) {
        temArr = data.filter(function (item) {
          return self.nowInDateBetwen(startDate, endDate, item.date)
        })
      }
      self.filterDatas = temArr;
    },

    nowInDateBetwen: function (d1, d2, date) {
      if (!d1 || !d2 || !date) return;
      //
      var dateBegin = new Date(d1.replace(/-/g, "/"));//-/new Date
      var dateEnd = new Date(d2.replace(/-/g, "/"));//-/new Date
      var dateNow = new Date(date.replace(/-/g, "/"));//-/new Date

      var beginDiff = dateNow.getTime() - dateBegin.getTime();//
      var beginDayDiff = Math.floor(beginDiff / (24 * 3600 * 1000));//

      var endDiff = dateEnd.getTime() - dateNow.getTime();//
      var endDayDiff = Math.floor(endDiff / (24 * 3600 * 1000));//
      if (endDayDiff < 0) {//
        return false
      }
      if (beginDayDiff < 0) {//
        return false;
      }
      if (endDayDiff == 0) {
        return true
      }
      if (beginDayDiff == 0) {
        return true;
      }
      return true;
    },
    submit: function () {
      var self = this;
      var time1, time2;
      if (!self.inputName) {
        self.$message.warning('');
        return;
      }
      if (self.Mode === '' || self.Mode === '') {
        time1 = self.format(self.startMonth);
        time2 = self.format(self.endMonth);
      } else if (self.Mode === '') {
        time1 = self.format(self.date[0]);
        time2 = self.format(self.date[1]);
      }
      self.filterDatas.push({
        type: self.Mode,
        fileName: self.inputName,
        startTime: time1,
        endTime: time2,
        mode: self.Mode,
        videoSource: "buzhiddao",
        audioSource: "kdsdfks",
        size: self.inputBit
      });
      this.$message(',');
      self.Mode = "";
      self.inputName = "";
      self.date = "";
      self.startMonth = "";
      self.endMonth = "";
      self.inputBit = "";
    },

    // 
    delTask: function (row) {
      this.filterDatas = this.filterDatas.filter(function (item) {
        return item.id != row.id;
      })
    },

    //
    stopTask: function (row) {
      var self = this;
      row.status = ""
    },
    //
    startTask: function (row) {
      var self = this;
      row.status = ""
    },
    //
    downLoadTask: function (row) {
      alert("")
    },
    //
    refreshRecord: function () {
      var self = this;
      self.tasks.splice(0, self.tasks.length);
      getRecordDatas().then(data => {
        var rows = data.rows;
        var total = data.total;
        if (rows && rows.length > 0) {
          for (var i = 0; i < rows.length; i++) {
            self.tasks.push(rows[i]);
            self.filterDatas.push(rows[i]);
          }
          self.total = total;
        }
      })
    },
    //
    format: function (str) {
      if (str) {
        str = str.toString();
        var str = str.replace(/ GMT.+$/, '');// Or str = str.substring(0, 24)
        var d = new Date(str);
        var a = [d.getFullYear(), d.getMonth() + 1, d.getDate(), d.getHours(), d.getMinutes(), d.getSeconds()];
        for (var i = 0, len = a.length; i < len; i++) {
          if (a[i] < 10) {
            a[i] = '0' + a[i];
          }
        }
        str = a[0] + '-' + a[1] + '-' + a[2] + ' ' + a[3] + ':' + a[4] + ':' + a[5];
        return str;
      }
    },
    initTree: function (keepExpand) {
      this.treeLoading = true;
      initFolderTree().then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
          this.treeLoading = false;
        } else {
          this.tree = res.tree;

          this.treeLoading = false;
          // console.log(JSON.stringify(res.tree));

          // if (!keepExpand) {
          //   // console.log('not keepExpand')
          //   this.expandNodeKeysArr.splice(0, this.expandNodeKeysArr.length);
          //   this.expandNodeKeysArr = this.tree.map(rootNode => rootNode.id);
          // }

          // this.defaultExpandKeys = this.expandNodeKeysArr;
          // this.treeLoading = false;
          // this.disableSetFolderBtn = true;
          // this.disableAddFolderBtn = true;
          // this.disableModifyFolderBtn = true;
          // this.disableDeleteFolderBtn = true;
          // this.disableResetRootBtn = false;
          // this.disableReleaseRootBtn = true;
        }
      });
    },
    handleNodeClick: function (data) {
      console.log(data)
      if (data.children) {
        return
      } else {
        console.log(data.name)
        this.inputDevice = data.name;
        this.dialogFormVisible = false;

      }
    }
  },
  mounted () {
    var self = this;
    self.refreshRecord();
  }
}
</script>  



