<template>
  <section>
    <el-tabs v-model="activeTabName" @tab-click="handleTabClick" style="float:left; width:100%;">
      <el-tab-pane label="" name="LwLayernodeManage"></el-tab-pane>
      <el-tab-pane label="" name="LwAddLayernode"></el-tab-pane>
    </el-tabs>

    <el-form :model="nodeForm" :rules="rules" ref="nodeForm" label-width="100px">
      <el-form-item label="" prop="type">
        <el-select v-model="nodeForm.type" style="width: 200px;">
          <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="name">
        <el-input v-model="nodeForm.name" style="width: 200px;"></el-input>
      </el-form-item>

      <el-form-item label="ID" prop="nodeUid">
        <el-input v-model="nodeForm.nodeUid" style="width: 200px;"></el-input>
      </el-form-item>

      <el-form-item label="IP" prop="ip">
        <el-input v-model="nodeForm.ip" style="width: 200px;"></el-input>
      </el-form-item>

      <el-form-item label="" prop="port">
        <el-input v-model="nodeForm.port" style="width: 200px;"></el-input>
      </el-form-item>

      <el-form-item label="" prop="downloadPort">
        <el-input v-model="nodeForm.downloadPort" style="width: 200px;" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="monitorUrl">
        <el-input v-model="nodeForm.monitorUrl" style="width: 200px;" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="netUrl">
        <el-input v-model="nodeForm.netUrl" style="width: 200px;" placeholder=""></el-input>
      </el-form-item>

      <el-form-item v-show="nodeForm.type=='ACCESS_WEBRTC'" label="webrtc http" prop="webrtcHttpPort">
        <el-input v-model="nodeForm.webrtcHttpPort" style="width: 200px;"></el-input>
      </el-form-item>

      <el-form-item v-show="nodeForm.type=='ACCESS_WEBRTC'" label="webrtc websocket" prop="webrtcWebsocketPort">
        <el-input v-model="nodeForm.webrtcWebsocketPort" style="width: 200px;"></el-input>
      </el-form-item>

      <!--<el-form-item label="" prop="url">-->
      <!--<el-input v-model="nodeForm.url" style="width: 200px;"></el-input>-->
      <!--</el-form-item>-->

      <div style="margin-top:30px;">
        <el-button type="primary" @click="submit()"></el-button>
        <el-button type="primary" @click="reset()"></el-button>
      </div>

    </el-form>

  </section>
</template>

<script type="text/ecmascript-6">
import { saveNode } from '../../api/api';

export default {
  name: "LwAddLayernode",
  data () {
    var checkIp = (rule, value, callback) => {
      let ip_reg = /^((2[0-4]\d|25[0-5]|[01]?\d\d?)\.){3}(2[0-4]\d|25[0-5]|[01]?\d\d?)$/;
      if (value && !ip_reg.test(value)) {
        callback(new Error("IP"));
      } else {
        callback();
      }
    };
    var checkPort = (rule, value, callback) => {
      let port_reg = /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-4]\d{4}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/;
      if (value && !port_reg.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      activeTabName: "AddLayernode",
      typeOptions: [
        // {
        //   label : "JV210",
        //   value : "ACCESS_JV210"
        // },
        // {
        //   label : "CDN",
        //   value : "ACCESS_CDN"
        // },
        // {
        //   label : "IPC",
        //   value : "ACCESS_IPC"
        // },
        {
          label: "JV230",
          value: "ACCESS_JV230"
        },
        {
          label: "",
          value: "ACCESS_TVOS"
        },
        {
          label: "",
          value: "ACCESS_MIXER"
        },
        {
          label: "",
          value: "ACCESS_MOBILE"
        },
        {
          label: "",
          value: "ACCESS_JV210"
        },
        {
          label: "A",
          value: "ACCESS_CDN"
        },
        {
          label: "B",
          value: "ACCESS_VOD"
        },
        {
          label: "",
          value: "ACCESS_IPC"
        },
        {
          label: "",
          value: "ACCESS_STREAMMEDIA"
        },
        {
          label: "",
          value: "ACCESS_NETWORK"
        },
        {
          label: "",
          value: "ACCESS_DISPLAYCTRL"
        },
        {
          label: "",
          value: "ACCESS_S100"
        },
        {
          label: "",
          value: "ACCESS_VODPROXY"
        },
        {
          label: "webrtc",
          value: "ACCESS_WEBRTC"
        },
        {
          label: "",
          value: "ACCESS_LIANWANG"
        },
        {
          label: "LDAP",
          value: "ACCESS_LDAP"
        },
        {
          label: "",
          value: "ACCESS_OMMS"
        },
        {
          label: "ws",
          value: "ACCESS_WS"
        }
      ],
      nodeForm: {
        type: "ACCESS_JV210",
        name: "",
        ip: "",
        nodeUid: "",
        port: null,
        downloadPort: null,
        monitorUrl: '',
        netUrl: '',
        webrtcHttpPort: 8088,
        webrtcWebsocketPort: 8188
      },
      rules: {
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }
        ],
        nodeUid: [
          { required: true, message: 'ID', trigger: 'change' },
          { min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }
        ],
        ip: [
          { validator: checkIp, trigger: 'blur' }
        ],
        port: [
          { validator: checkPort, trigger: 'blur' }
        ],
        downloadPort: [
          { validator: checkPort, trigger: 'blur' }
        ],
      }
    };
  },
  methods: {
    handleTabClick (tab, event) {
      if ("LwAddLayernode" !== tab.name) {
        this.$router.push('/' + tab.name);
      }
    },
    reset: function () {
      this.$refs["nodeForm"].resetFields();
    },
    submit: function () {
      if (!this.validate()) {
        return;
      }

      let param = {
        json: JSON.stringify(this.nodeForm)
      };

      saveNode(param).then(res => {
        if (res.errMsg) {
          this.$message({
            message: res.errMsg,
            type: 'error'
          });
        } else {
          // this.$alert("ID" + res.nodeUid, '', {
          //   confirmButtonText: ''
          // });
          this.$message({
            message: "",
            type: 'success'
          });
          this.$refs["nodeForm"].resetFields();
        }
      });
    },
    validate: function () {
      var result = false;
      this.$refs["nodeForm"].validate((valid) => {
        result = valid;
      });
      return result;
    }
  },
  mounted () {
    var self = this;
    this.$nextTick(function () {
      self.$parent.$parent.$parent.$parent.$parent.setActive('/LayernodeManage');
    });
  }
}
</script>

<style scoped>
</style>



